.child {
  position: relative;
  border-radius: var(--br-xl) 0 0 0;
  background-color: var(--primary-4);
  width: 800px;
}
.child,
.div,
.item {
  height: 500px;
}
.item {
  position: relative;
  border-radius: 0 var(--br-xl) var(--br-xl) 0;
  width: 520px;
  object-fit: cover;
}
.div {
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 7px 20px #d9d9e9;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.howToMonetize,
.youCanIncrease {
  position: relative;
  display: inline-block;
}
.howToMonetize {
  font-size: var(--h3-size);
  line-height: 50px;
  width: 648px;
}
.youCanIncrease {
  line-height: 30px;
  width: 586px;
}
.howYouCan {
  margin-block-start: 0;
  margin-block-end: 15px;
  font-weight: 600;
}
.integrateSmatched {
  margin: 0;
}
.howYouCanContainer {
  position: relative;
  line-height: 30px;
  display: inline-block;
  width: 612px;
}
.parent,
.text {
  position: absolute;
}
.text {
  top: 79px;
  left: 76px;
  height: 353px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.parent {
  top: 962px;
  left: calc(50% - 661px);
  width: 1320px;
  height: 500px;
  text-align: left;
  font-size: var(--body-midd-size);
  color: var(--neutral-1);
  font-family: var(--body-midd);
}
