}
.onlyAround10Container {
  position: relative;
  font-size: var(--h4-size);
  line-height: 38px;
  display: inline-block;
  width: 728px;
}
.buttons,
.text1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.text1 {
  position: absolute;
  top: 0;
  left: 0;
  width: 731px;
  flex-direction: column;
  gap: var(--gap-21xl);
}
.underline01Icon {
  position: absolute;
  height: 8.08%;
  width: 89.88%;
  top: 12.43%;
  right: 7.39%;
  bottom: 79.5%;
  left: 2.74%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.text {
  position: relative;
  width: 731px;
  height: 482px;
  z-index: 0;
}
.mainBlockChild {
  position: absolute;
  margin: 0 !important;
  top: -0.51px;
  left: 836px;
  border-radius: 0 0 var(--br-184xl) var(--br-206xl);
  width: 450.75px;
  height: 655.06px;
  object-fit: cover;
  z-index: 1;
}
.mainBlock {
  position: absolute;
  top: 202px;
  left: calc(50% - 640px);
  display: flex;
  flex-direction: column;
  padding: var(--padding-38xl) 0;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-3xs);
  text-align: left;
  font-size: var(--h2-size);
  color: var(--neutral-1);
  font-family: var(--body-midd);
