.child {
  position: relative;
  border-radius: var(--br-xl) 0 0 var(--br-xl);
  background-color: var(--primary-4);
  width: 800px;
  height: 500px;
  transform: rotate(180deg);
  transform-origin: 0 0;
}
.componentChild,
.div {
  position: absolute;
  top: 0;
}
.div {
  left: 520px;
  box-shadow: 0 7px 20px #d9d9e9;
  width: 800px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
}
.componentChild {
  left: 0;
  border-radius: var(--br-xl) 0 0 var(--br-xl);
  width: 520px;
  height: 500px;
  object-fit: cover;
}
.smatchedPresentsA,
.unleashThePotential {
  position: relative;
  line-height: 50px;
  display: inline-block;
  width: 586px;
}
.smatchedPresentsA {
  font-size: var(--body-midd-size);
  line-height: 30px;
}
.text,
.textWrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.text {
  width: 586px;
  height: 244px;
  flex-direction: column;
  gap: var(--gap-5xl);
}
.textWrapper {
  position: absolute;
  top: 128px;
  left: 620px;
  border-radius: var(--br-xl);
  flex-direction: row;
}
.parent {
  position: absolute;
  top: 1562px;
  left: calc(50% - 660px);
  width: 1320px;
  height: 500px;
  text-align: left;
  font-size: var(--h3-size);
  color: var(--neutral-1);
  font-family: var(--body-midd);
}
